
import jsPDF from 'jspdf';
import autoTable from 'jspdf-autotable';
import { format } from 'date-fns';
import type { Invoice } from './constants';

export function generateInvoicePdf(invoice: Invoice) {
    const doc = new jsPDF();

    // Theme Colors
    const primaryColor = '#89B7E5'; // Light Blue
    const headingColor = '#2d3748'; // Dark Gray-Blue
    const textColor = '#4a5568'; // Medium Gray
    const lightTextColor = '#718096'; // Light Gray
    const backgroundColor = '#F0F8FF'; // Very light blue background

    // 1. Header with custom shape and Logo
    doc.setFillColor(primaryColor);
    doc.rect(0, 0, 210, 40, 'F'); // Solid header background
    doc.setFont('helvetica', 'bold');
    doc.setFontSize(24);
    doc.setTextColor('#FFFFFF');
    doc.text('MediTrack Pro', 14, 25);

    doc.setFontSize(10);
    doc.setFont('helvetica', 'normal');
    doc.text('123 Health St, Wellness City, 12345', 14, 32);

    // 2. Invoice Title Section
    doc.setFontSize(26);
    doc.setFont('helvetica', 'bold');
    doc.setTextColor(headingColor);
    doc.text('INVOICE', 200, 25, { align: 'right' });

    doc.setFontSize(11);
    doc.setFont('helvetica', 'normal');
    doc.setTextColor(textColor);
    doc.text(`Invoice #: ${invoice.id}`, 200, 32, { align: 'right' });
    doc.text(`Date Issued: ${format(new Date(), 'yyyy-MM-dd')}`, 200, 38, { align: 'right' });


    // 3. Patient and Doctor Information
    doc.setLineWidth(0.5);
    doc.setDrawColor(primaryColor);
    doc.line(14, 55, 200, 55);

    doc.setFontSize(10);
    doc.setFont('helvetica', 'bold');
    doc.setTextColor(headingColor);
    doc.text('BILL TO', 14, 63);
    doc.text('ATTENDING PHYSICIAN', 110, 63);

    doc.setFont('helvetica', 'normal');
    doc.setTextColor(textColor);
    doc.text(invoice.patientName, 14, 69);
    doc.text(invoice.doctorName || 'N/A', 110, 69);
    
    doc.line(14, 75, 200, 75);

    // 4. Billed Items Table
    const tableData = invoice.items.map(item => [
        item.name,
        item.type,
        `₦${item.price.toFixed(2)}`
    ]);

    autoTable(doc, {
        startY: 85,
        head: [['Item Description', 'Type', 'Price']],
        body: tableData,
        theme: 'grid',
        headStyles: {
            fillColor: primaryColor,
            textColor: 255,
            fontSize: 12,
            fontStyle: 'bold',
            halign: 'center',
        },
        styles: {
            fontSize: 10,
            cellPadding: 3,
            lineColor: '#E2E8F0'
        },
        columnStyles: {
            0: { halign: 'left' },
            1: { halign: 'center' },
            2: { halign: 'right' }
        },
        didDrawPage: (data) => {
            const tableEndY = (data.cursor as { y: number }).y;
            
            // 5. Totals Section
            const summaryY = tableEndY + 10;
            doc.setFontSize(11);
            doc.setFont('helvetica', 'bold');
            doc.setTextColor(headingColor);

            doc.text('Total Amount', 145, summaryY, { align: 'right' });
            doc.text(`₦${invoice.amount.toFixed(2)}`, 200, summaryY, { align: 'right' });
            
            doc.setLineWidth(0.5);
            doc.setDrawColor(primaryColor);
            doc.line(145, summaryY + 3, 200, summaryY + 3);

            // 6. Due Date & Status
            doc.setFontSize(12);
            doc.setFont('helvetica', 'bold');
            doc.text('Due Date:', 14, summaryY + 20);
            doc.setTextColor(primaryColor);
            doc.text(format(new Date(invoice.dueDate), "PPP"), 40, summaryY + 20);
            
            doc.setTextColor(headingColor);
            doc.text('Status:', 14, summaryY + 28);
            doc.setFont('helvetica', 'bold');
            doc.setTextColor(invoice.status === 'Paid' ? '#22C55E' : invoice.status === 'Overdue' ? '#EF4444' : '#EAB308');
            doc.text(invoice.status.toUpperCase(), 35, summaryY + 28);
        }
    });


    // 7. Footer
    const pageCount = (doc as any).internal.getNumberOfPages();
    for(let i = 1; i <= pageCount; i++) {
        doc.setPage(i);
        doc.setFontSize(9);
        doc.setTextColor(lightTextColor);
        
        doc.setLineWidth(0.2);
        doc.setDrawColor(lightTextColor);
        doc.line(14, 278, 200, 278);

        doc.text('Thank you for choosing MediTrack Pro!', 14, 285);
        doc.text(`Page ${i} of ${pageCount}`, 200, 285, { align: 'right' });
    }

    // 8. Save PDF
    const fileName = `Invoice-${invoice.patientName.replace(/ /g, '_')}-${format(new Date(), 'yyyy-MM-dd')}.pdf`;
    doc.save(fileName);
}
